@import "./src/styles/utils";

.buttonGreyRef{
  &.buttonArrow {
    min-width: 127px;
    background-color: $black ;
    margin-left: 19px;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -khtml-border-radius: 25px;
    -webkit-mask-image: -webkit-radial-gradient(circle,  white 100%, $black 100%);
    overflow: hidden;
    color: $white;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
    position: relative;
    @extend .button-text;
    &.light {
      background-color: $light-gray;
    }
    &.grey {
      background-color: $medium-gray;
    }
    span {
      width: 100%;
      position: relative;
      z-index: 2;
      @include margin-t(12px,12px, 9px);
      @include margin-b(12px,12px, 9px);
      @include margin-l(16px, 16px, 16px);
      @include margin-r(56px, 55px, 44px);
    }
    .center {
      z-index: 10;
      margin-right: auto;
      margin-left: auto;
    }
  }

  &.buttonGrey {
    color: $black;
    margin-left: 0 !important;
  }

  .buttonCircle{
    width: 80px;
    height: 80px;
    border-radius: 50%;
  }

  .buttonGreyAnimation {
    z-index: 3;
    position: absolute;
    background-color: $gray;
    color: $white;
    left: 100%;
  }

  .circleGroup {
    position: absolute;
    width: 28px;
    height: 28px;
    right: 18px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }

  .circle {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 50%;
    background-color: $orange ;
  }

  .arrow {
    z-index: 2;
    position: absolute;
    width: 10px;
  }

  @media all and (max-width: 640px) {
    .circleGroup  {
      width: 20px;
      height: 20px;
      right: 16px;
    }
  }
}


